<template>
	<div class="header" >
		<div class="bgcolor">
			
			<!-- 
			 -->
		</div>
		<div class="container">
			<div class="header_left">
				<img :src="seller.avatar" alt="" class="avatar" @click="show">
			</div>
			<div class="header_right">
				<div>
					<img src="../../../static/imgs/brand.png" alt="" class="brand">
					<span class="title">{{seller.name}}</span>
				</div>
				
				<p class="text_right">{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
				<div class="right_bottom">
					<img src="../../../static/imgs/decrease.png" alt="" class="right_pics">
					<span class="text_right_1" v-if="seller.supports">{{seller.supports[0].description}},满50减10</span>
				</div>
				<div class="text_icon"  @click="show">5个 ></div>
			</div>
		
		</div>
		<div class="header_bottom"  @click="show">
			<img src="../../../static/imgs/bulletin.png" alt="" class="">
			<span>{{seller.bulletin}}</span><span>></span>
		</div>
		<Detail></Detail>	
	</div>
</template>
<script>
	import {mapGetters} from 'vuex'
	import Detail from '../detail/Detail.vue'

	export default {
		computed:{
			...mapGetters([
				'seller',
			])
		},
		components:{
			Detail
		},
		created(){
			this.$store.dispatch('getSeller');
		},
		methods:{
			show(){
				this.$store.dispatch('showdetail');
			}
		}
	}
</script>
<style lang='less' scoped>
	.header{
		height:2.68rem;
		// background:gray;
		// position: absolute;
		// position:relative;
		// position:relative;
		// z-index: -3;
		// overflow: hidden;
		background:rgba(7,17, 27, 0.5);
		.bgcolor{
			width:100%;
			height:2.6rem;
			background: url('../../../static/imgs/bg.png') no-repeat;
			background-size: 100% 100%;
			filter:blur(0.1rem);
			z-index: -8;
			position:absolute;
			overflow: hidden;
		}
		.header_left{
			width:1.28rem;
			height:1.28rem;
			boder-radius:0.04rem;
			padding-top: 0.48rem;
			padding-left: 0.48rem;
			.avatar{
				width:1.28rem;
				height:1.28rem;
				boder-radius:0.04rem;
			
			}
		
		}
		.container{ 
			width:100%;
			height:2.12rem;
			display:flex;
			.header_right{
				padding-left: 0.32rem;
				padding-top:0.52rem;
				// dispaly:flex;

				.brand{
					height:0.36rem;
				}
				.title{
					font-size: 0.32rem;
					color: rgb(255,255,255);
					font-weight: bold;
					line-height: 0.36rem;
					padding-left: 0.12rem;
					
				}
				.text_right{
					font-size: 0.24rem;
					color:rgb(255,255,255);
					font-weight: 200;
					line-height: 24px;
					padding-top: 0.08rem;
				}
				// .right_bottom{
				// 	padding-top: 0.2rem;
				// }
				.right_pics{
					height:0.24rem;
				}
				.text_right_1{
					font-size: 0.20rem;
					color:rgb(255,255,255);
					font-weight: 200;
					line-height: 24px;
				}
				.text_icon{
					font-size: 0.20rem;
					color:rgb(255,255,255);
					font-weight: 200;
					line-height: 24px;
					position:absolute;
					right:0.24rem;
					top:1.28rem;
					background-color: rgba(0,0,0,0.2);
					height:0.48rem;
					width:0.96rem;
					text-align: center;
					border-radius: 0.24rem;
					line-height: 0.48rem;
				}
			}
			}
		}
		

		.header_bottom{
				height:0.56rem;
				width:100%;
				background-color: rgba(0,0,0,0.2);
				display: flex;
				line-height: 0.56rem;
				img{
					padding-left: 0.24rem;
					height:0.3rem;
					padding-top: 0.13rem;
					
				}
				span:nth-of-type(1){
					padding-top: 0.09rem;
					display: inline-block;
					padding-left: 0.08rem;
					padding-right: 0.08rem;
					overflow: hidden;
					// display: block;
					white-space: nowrap;
					text-overflow:ellipsis;
					font-size: 0.20rem;
					color:rgb(255,255,255);
					font-weight: 200;
					line-height: 24px;
				}
				span:nth-of-type(2){
					padding-right: 0.24rem;
					padding-top: 0.09rem;
					
					font-size: 0.20rem;
					color:rgb(255,255,255);
					font-weight: 200;
					line-height: 24px;
				}
		
	}
</style>


