<template>
	<div class="star">
		<span class="xx" v-for='v in starItem' :class='v'></span>
		
	</div>
</template>
<script>
	const length=5;
	const on_star='on';
	const half_star='half';
	const off_star ='off';
	import {mapGetters} from 'vuex'

	export default{
		props:['score'],
		computed:{
			...mapGetters([
					'seller'
				]),
			starItem(){
				let result =[];
				let sum =this.score;
				let sum1 = parseInt(sum);
				let sum2 = sum-sum1;
				for(let i =0;i<sum1;i++){
					result.push(on_star);
				}
				if(sum2>=0.5&&sum2<1){
					result.push(half_star);
				}
				while(result.length<length){
					result.push(off_star);
				}
				return result;

			}
		}
	}
</script>
<style lang='less' scoped>
	.star{
		color:white;
		.xx{
			width:0.28rem;
			height: 0.28rem;
			display: inline-block;
			margin-right: 0.1rem;
			&.on{
				color: red;
				background: url('star_on.png');
				background-size: 100% 100%;
				background-repeat: no-repeat;
			}
			&.off{
				background: url('star_off.png');
				background-size: 100% 100%;
				background-repeat: no-repeat;
			}
			&.half{

				background: url('star_half.png');
				background-size: 100% 100%;
				background-repeat: no-repeat;
			}
		}
	}
</style>


