<template>
	<div class="goods">
		<div class="goods_menu menu-wrapper" ref='menuWrapper'>
			<ul>
				<li  @click='selectMenu(k,$event)' v-for="(v,k) in goods" class="menu-list" :class="{'current' : k === currentIndex}">
					<span>{{v.name}}</span>
				</li>
			</ul>
		</div>
		<div class="goods_foods menu-wrapper" ref='menuWrapper2'> 
		<div>
			<div v-for="(v,k) in goods" :key="k" class="food-list-hook">
					<div class='goods_foods_name'>
						<span >{{v.name}}</span>
					</div>	
					<ul >
					<li v-for="food in v.foods" class='goods_foods_content' @click="showfood(food,$event)">
						<div class="foods_container">
							<div class="container_left">
								<img :src="food.image" alt="" >
							</div>
							<div class="container_right">
								<p class="foods_text1">{{food.name}}</p>
								<p class="foods_text2" v-if='food.description'>{{food.description}}</p>
								<div class="foods_count">
									<span class="foods_text4">月售{{food.sellCount}}份</span>
									<span class="foods_text5">好评率{{food.sellCount}}%</span>
								</div>
									
							  	<div class='foods_price'>
									<span>￥</span>
									<span class="foods_text6">{{food.price}}</span>
									<span v-if='food.description'>￥28</span>
									<!-- <div class="icon_add">
										<span>
											<i class="iconfont icon-jianhao"></i>
										</span>
										<span>111</span>
										<span>
											<i class="iconfont icon-jiahao"></i>
										</span>
									</div> -->
									<iconadd :food='food' class="iconadd" :class="{'width_icon':food.description}"></iconadd>
								</div>
							</div>	
						</div>
						<!-- {{i.name}} -->
					</li>
				</ul>
			</div>
		</div>
	</div>
	<detail></detail>
	
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import BScroll from 'better-scroll'
	import detail from "../detail/foodDetail.vue"
	import iconadd from "../iconadd/iconadd.vue"
	
	export default {
		data(){
			return {
				listHeight : [],
				scrollY : 0 
			}
		},
		components:{
	    	detail,
	    	iconadd

        },
		computed:{
			...mapGetters([
					'goods'
				]),
			currentIndex(){
		    	for( let i = 0 ; i < this.listHeight.length ; i++ ){
		    		let height1 = this.listHeight[i];
		    		let height2 = this.listHeight[i+1];
		    		if( this.scrollY >= height1 && this.scrollY < height2 ){
		    			return i;
		    		}
		    	}
		    	return 0;
		    },
			
		},
		created(){
			this.$store.dispatch('getGoods');
			this.$nextTick( () => {
				setTimeout(() => {
					this._goodsMenu();
					this._goodsMenu2();
					this._calcHeight();
				},100)
			})

		},
		methods:{
			showfood(food,e){
				if( !e._constructed ){
					return;
				};
				this.$store.dispatch('showfood',food);
				console.log(1111);
			},
			selectMenu(key,e){
				if( !e._constructed ){
					return;
				}
				let foodList =this.$refs.menuWrapper2.getElementsByClassName('food-list-hook');
				let el = foodList[key];
				// console.log(el);
				this.menuBS2.scrollToElement(el,300);

			},
			_calcHeight(){
            	let foodList = this.$refs.menuWrapper2.getElementsByClassName('food-list-hook');
            	let height = 0;
            	this.listHeight.push(height);
            	for(let i = 0 ; i < foodList.length ; i++){
            		height += foodList[i].clientHeight;
            		this.listHeight.push(height);
            	}
            	// console.log(this.listHeight);
            },
			_goodsMenu(){
				this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
			},
			_goodsMenu2(){
				this.menuBS2 = new BScroll(this.$refs.menuWrapper2,{
					probeType:3,
					click:true
				});
				this.menuBS2.on('scroll',(pos)=>{
					this.scrollY = Math.abs(Math.round(pos.y));
					// console.log(this.scrollY);
				})
			}
		}
	}	
</script>

<style lang='less' scoped>
	.goods{
		position: absolute;
		top:3.48rem;
		bottom:1.11rem;
		width:100%;
		display: flex;
		overflow: hidden;
		.goods_menu{
			flex:0 0 1.6rem;
			width:1.6rem;
			.menu-list{
				&.current {
					background:#f3f5f7;
					border-right: 0.08rem solid lightgray; 
				}
			}
			li{
				
				height: 1.08rem;
				background: #f4f5f7;
				// border: 1px solid #000;
				padding:0 0.24rem;
				display: table;
				span{
					width:1.12rem;
					border-bottom: 1px solid lightgray;
					text-align: center;
					vertical-align: middle;
					display: table-cell;
					width: 1.12rem;
				}
			}
		}
		.goods_foods{
			flex:1;
			.goods_foods_name{
				font-size: 0.24rem;
				color:rgb(147,153,159);
				line-height: 0.48rem;
				font-weight: normal;
				background-color:  #f3f5f7;
				display: inline-block;
				width:100%;
				padding-left: 0.24rem;
			}
			.goods_foods_content{
				padding: 0.36rem;
				border-bottom: 0.01rem solid rgba(7,17,27,0.1);
				.foods_container{
					display: flex;
					img{
					width:1.08rem;
					}
					.container_right{
						padding-left: 0.2rem;
						.foods_text1{
							font-size: 0.28rem;
							color:rgb(7,17,27);
							line-height: 0.28rem;
							font-weight:600;
							padding-top: 0.04rem;
						}
						.foods_text2{
							font-size: 0.2rem;
							color:rgb(147, 153,159);
							line-height: 0.2rem;
							padding-top: 0.16rem;
						}
						.foods_count{
							display: flex;
							font-size: 0.2rem;
							color:rgb(147, 153,159);
							line-height: 0.2rem;
							padding-top: 0.16rem;
							span:nth-of-type(2){
								padding-left: 0.24rem;
							}
						}
						div{
							display: flex;
						}
						.foods_price{
							padding-top: 0.16rem;
							line-height: 0.48rem;
							width:2.06rem;
							span:nth-of-type(1){
								font-size: 0.20rem;
								// padding-right: 0.16rem;
								color:red;
								line-height: 0.48rem;
								padding-bottom: 0rem;
								
							}
							span:nth-of-type(2){
								color:red;
								font-size: 0.3rem;
								font-weight: 700;
								padding-right: 0.16rem;
								// text-decoration:line-through;
							}
							span:nth-of-type(3){
								color:rgb(147, 153,159);
								font-size: 0.2rem;
								font-weight: 700;
								padding-right: 0.16rem;
								text-decoration:line-through;
							}
							.iconadd{
								position: absolute;
								right:0.36rem;
							}
							.width_icon{
								padding-left:1.3rem;
							}

						}
					}
				}
				
			}

		}
	}
</style>


