import types from '../types.js'
import axios from 'axios'
//相当于 .Vue里面的data
const state = {
	goods:[],
	foods:[],
	foodshow:false
	
}
//返回的数据
const getters = {
	goods(state){
		return state.goods;
	},
	foods(state){
		return state.foods;
	},
	foodshow(state){
		return state.foodshow;
	}
}
// 接收components里面传过来的方法并提交给mutations执行
const actions = {
	getGoods({commit,state}){
		axios.get('../../../static/data.json').then(resp=>{
			if(resp.data.error === 0){
				commit(types.GET_GOODS,resp.data.goods);
			}
		})
	},
	showfood({commit},food){
			commit(types.SHOW_FOOD,food);
	},
	hidefood({commit}){
		commit(types.HIDE_FOOD);
	}
}
// 执行具体动作
const mutations = {
	[types.GET_GOODS](state,data){
		state.goods = data;
	},
	[types.SHOW_FOOD](state,food){
		state.foods =food;
		state.foodshow= true;
		// console.log(1111111111111)
	},
	[types.HIDE_FOOD](state){
		state.foodshow = false;
	}


}
export default {
	state,
	getters,
	actions,
	mutations
}


