<template>
	<div class="ratings">
		<div class="menu-wrapper " ref='menuWrapper'> 
			<div class="container">
				<div class="container_head">
					<div class="seller_head_top">
						<div class="head_left">
							<p>{{seller.name}}</p>
							<div class="left_star">
								<star :score="seller.foodScore"></star>
								<span>({{seller.sellCount}})</span>
								<span>月售{{seller.sellCount}}单</span>
							</div>
							
						</div>
						<div class="head_right" @click='doLove'>
							<i class="iconfont icon-xin" :class="{'love':love}"></i>
							<p v-if="love">已收藏</p>
							<p v-if="!love">未收藏</p>
						</div>
					</div>
					<div class="seller_head_bottom">
						<div>
							<p>起送价</p>
							<span>{{seller.minPrice}}</span>
							<span>元</span>
						</div>
						<div>
							<p>商家配送</p>
							<span>{{seller.deliveryPrice}}</span>
							<span>元</span>
						</div>
						<div>
							<p>平均配送时间</p>
							<span>{{seller.deliveryTime}}</span>
							<span>分钟</span>
						</div>
					</div>
					
				</div>
				<div class="divide"></div>
				<div class="add_active">
					<p>公告与活动</p>
					<div>{{seller.bulletin}}</div>
				</div>
				<div class="iconList">
					<ul>
						<li class="icon_item" v-for="i in seller.supports">
							 <div class="icon" :class="iconClassMap[i.type]"></div>
                   			 <span class="txt">{{i.description}}</span>
						</li>
					</ul>
				</div>
				<div class="divide"></div>

				<div class="seller_photo">
					<p>商家实景</p>
					 <div ref="menu2Wrapper" class="imgWrapper">
		                <div>
		                    <ul class="img_list">
		                        <li v-for="(v,k) in seller.pics" :key="k">
		                            <img :src="v" alt="">
		                        </li>    
		                    </ul>
		                </div>
           			 </div>
				</div>
				<div class="divide"></div>
				<div class="seller_detail">
					<p>商家信息</p>
					<ul>
						<li v-for="i in seller.infos"><div>{{i}}</div></li>
					</ul>
				</div>
				<div class="space"></div>
			</div>
				
		</div>
		
	</div>
</template>

<script>
	import {mapGetters} from "vuex"
	import star from '../star/starl.vue'
	import BScroll from 'better-scroll'
	export default {
		data(){
			return {
				love:false
			}
		},
		computed:{
			...mapGetters([
				'seller',
				'ratings',
				// 'goods'
				])
		},
		created(){
			this.$store.dispatch('getSeller');
			this.$store.dispatch('getRatings');
			// this.$store.dispatch('getGoods');
			this.$nextTick( () => {
				setTimeout(() => {
					this._sellerInfo();
				},100)
			});
			this.iconClassMap = ["decrease","discount","special","invoice","guarantee"]
		},
		components:{
			star
		},
		methods:{
			_sellerInfo(){
				this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
				this.menuBS1 = new BScroll(this.$refs.menu2Wrapper, {
	                    // better-scroll 会将点击事件去掉，要在这里开启，同时点击在PC 会被执行两次，要在这里控制
	                    scrollX: true,
	                    click: true
	            });
			},
			doLove(){
				console.log(111)
				this.love=!this.love
			}
		}
	}	
</script>

