import types from '../types.js'
import axios from 'axios'
//相当于 .Vue里面的data
const state = {
	seller:{},
	detailshow :false
}
//返回的数据
const getters = {
	seller(state){
		return state.seller;
	},
	detailshow(state){
		return state.detailshow;
	}
}
// 接收components里面传过来的方法并提交给mutations执行
const actions = {
	getSeller({commit,state}){
		axios.get('../../../atatic/data.json').then(resp=>{
			// if(resp.data.error === 0){
				commit(types.GET_SELLER,resp.data.seller);
			// }
		})
	},
	// 接收header里面传过来的显示详情的方法并提交给mutations
	showdetail({commit}){
		// 使用常量名称告诉mutations
		commit(types.SHOW_DETAIL);
	},
	closedetail({commit}){
		commit(types.HIDE_DETAIL);
	}

}
// 执行具体动作
const mutations = {
	[types.GET_SELLER](state,data){
		state.seller = data;
	},
	[types.SHOW_DETAIL](state){
		state.detailshow = true;
	},
	[types.HIDE_DETAIL](state){
		state.detailshow = false;
	}
}
export default {
	state,
	getters,
	actions,
	mutations
}


