<template>
<transition enter-active-class="animated slideInDown" leave-active-class="animated slideOutRight">
	<div class="detail" v-show='detailshow'>
		<div class="head_title">
			<span>{{seller.name}}</span>
		</div>
		<div class="mark_star">
			<!-- <img src="../../../imgs/star_on.png" alt="" class="star_pic"> -->
			<star :score='seller.score'></star>
		</div>
		<div class="fav_line">
			<hr><span>优惠信息</span><hr>
		</div>
		<div class="iconList">
			<ul>
				<li class="icon_item" v-for="i in seller.supports">
					<div class="icon" :class="iconClassMap[i.type]"></div>
                   	<span class="txt">{{i.description}}</span>
				</li>
			</ul>
		</div>
		<div class="seller_line">
			<hr><span>商家公告</span><hr>
		</div>
		<div class="seller_container">
			<span>{{seller.bulletin}}</span>
		</div>
		
		<div class="close_x" >
			<i class="iconfont icon-X" @click="close"></i>
		</div>
	</div>
</transition>
</template>

<script>
	import {mapGetters} from 'vuex'
	import star from '../star/star.vue'
	// import 'animate.css/animate.css'
	export default {
		computed : {
			...mapGetters([
				'detailshow',
				'seller'
			]),
			
		},
		methods : {
			close(){
				this.$store.dispatch('closedetail')
			}
		},
		components:{
			star
		},
		created(){
			this.iconClassMap = ["decrease","discount","special","invoice","guarantee"]
		}
	}
</script>

<style lang='less' scoped>
	.detail{
		position: fixed;
		top:0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(7,17,27,0.8);
		z-index: 10000; 
		.head_title{
			text-align: center;
			padding-top: 1.28rem;

			span{
				font-size: 0.32rem;
				color: rgb(255,255,255);
				font-weight: 700;
				line-height: 0.32rem;
			}
		}
		.mark_star{
			padding-top: 0.32rem;
			text-align: center;
			height:0.48rem;
			padding-top: 0.32rem;
		}
		.fav_line{
			display:flex;
			padding-top: 0.56rem;
			span{
				font-size: 0.32rem;
				color: rgb(255,255,255);
				font-weight: 700;
				line-height: 0.32rem;
			}
			hr{
				width:2.24rem;
				height:0.04rem;
				border:0;
				background: rgba(255,255,255,0.3);
			}
			
		}
		.iconList{
			padding-top: 0.48rem;
				.icon_item{
					width:6.68rem;
					margin-left: 0.36rem;
					padding-left: 0.24rem;
					border-bottom: 0.01rem solid rgba(7,17,27,0.1);
					// padding-bottom: 0.32rem;
					// padding-top: 0.32rem;
					font-size: 0.24rem;
					// padding-top: 0.32rem;
					padding-bottom: 0.12rem;
					.txt{
						font-size: 0.24rem;
						font-weight: 200;
						color:rgb(255,255,255);
						line-height: 0.24rem;
					}
					.icon{
						display: inline-block;
						width: 0.28rem;
						height: 0.36rem;
						padding-right: 0.12rem;

						// margin-top: 0.12rem;
						// background-size:100 0.32rem;
						// vertical-align: middle;

						&.decrease{
							background-image:url("../../../static/imgs/decrease.png");
							background-repeat: no-repeat;
							background-size: 100% 100%;
						}
						&.discount{
							background-image:url("../../../static/imgs/discount.png");
							background-repeat: no-repeat;
							background-size: 100% 100%;
						}
						&.special{
							background-image:url("../../../static/imgs/special.png");
							background-repeat: no-repeat;
							background-size: 100% 100%;
						}
						&.invoice{
							background-image:url("../../../static/imgs/invoice.png");
							background-repeat: no-repeat;
							background-size: 100% 100%;
						}
						&.guarantee{
							background-image:url("../../../static/imgs/guarantee.png");
							background-repeat: no-repeat;
							background-size: 100% 100%;
						}
					
					}
				}
				
			}
		.seller_line{
			display:flex;
			padding-top: 0.56rem;
			span{
				font-size: 0.32rem;
				color: rgb(255,255,255);
				font-weight: 700;
				line-height: 0.32rem;
			}
			hr{
				width:2.24rem;
				height:0.04rem;
				border:0;
				background: rgba(255,255,255,0.3);
			}
			
		}
		.seller_container{
			padding-top: 0.48rem;
			padding-left: 0.96rem;
			padding-right: 0.96rem;
			font-size: 0.24rem;
			font-weight: 200;
			color:rgb(255,255,255);
			line-height: 0.48rem;
		}
		
		.close_x{
			margin-bottom: 0.64rem;
			// text-align: center;
			// font-size: 0.32rem;
			// padding-bottom: 0;
			color:rgba(255,255,255,0.5);
			position: absolute;
			bottom:0.64rem;
			width:100%;
			text-align: center;
			// font-size: 0.88rem;
			// margin:0 auto;
			// display: block;
			// padding-left:50%;
			i{
				font-size: 0.32rem;

			}	
		}
	}
		
</style>


