<template>
	<div class="ratings">
		<div class="rating_header">
			<div class="header_left">
				<p>{{seller.score}}</p>
				<p>综合评分</p>
				<p>高于周边商家{{seller.rankRate}}%</p>
			</div>
			<div class="header_right">
				<ul>
					<li>
						<span>服务态度</span>
						<star :score='seller.serviceScore' class="star1"></star>
						<span>{{seller.serviceScore}}</span>
					</li>
					<li>
						<span>服务态度</span>
						<star :score='seller.serviceScore' class="star2"></star>
						<span>{{seller.serviceScore}}</span>
					</li>
					<li>
						<span>送达时间</span>
						<span>{{seller.deliveryTime}}分钟</span>
					</li>
				</ul>
			</div>
			
		</div>
		<div class="divide"></div>
		<div class="rating_count">
			<div class="count_top">
				<div class="count_all" @click="checkall()" :class="{'comcolor':sizeone}">
					<span>全部{{seller.ratingCount}}</span>
				</div>
				<div class="count_good" @click="checkyes()" :class="{'comcolor':sizetwo}">
					<span>满意{{good}}</span>
				</div>
				<div class="count_bad" @click="checkno()" :class="{'comcolor':sizethree}">
					<span>不满意{{seller.ratingCount-good}}</span>
				</div>
				
			</div>
			<div class="count_bottom" >
				<i class="iconfont icon-duihao" :class="{'checked':flag}" @click="changeFlag"></i>
				<span>只看有内容的评价</span>
			</div>
		</div>
		<!-- <div class="divide"></div> -->
		<div class="menu-wrapper rating_foot" ref='menuWrapper'> 
			<div class='height'>
				<div v-for="v in ratList" class="ratings_container"  v-show="all || yes===v.rateType">
					<div class="ratings_left">
						<img :src="v.avatar" alt="">
					</div>
					<div class="ratings_right">
						<div class="right_div1">
							<span class="right_name">{{v.username}}</span>
							<span class="right_time">2016-07-13 20:33</span>
						</div>
						<div class="right_div2" v-if="v.deliveryTime">
							<star :score="v.score"></star>
							<span>{{v.deliveryTime}}分钟送达</span>
						</div>
						<div class="right_div3">
							<span>{{v.text}}</span>
						</div>
						<div class="right_div4">
							<i class="iconfont icon-damuzhion" v-if="v.rateType==0"></i>
							<i class="iconfont icon-down" v-if="v.rateType==1"></i>
							<span v-for="i in v.recommend">{{i}}</span>
						</div>
					</div>
					
				</div>
				<div class="space"></div>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import star from '../star/stars.vue'
	import BScroll from 'better-scroll'
	export default {
		data(){
			return{
				all:true,
				yes:-1,
				flag:false,
				sizeone:true,
				sizetwo:false,
				sizethree:false
			}
		},
		computed:{
			...mapGetters([
				'seller',
				'ratings'
				

			]),
			good(){
				let arr=[];
				this.ratings.forEach(type=>{
					if(type.rateType==0){
						arr.push(type);
					}
				});
				// console.log(arr.length)
				return arr.length;

			},
			ratList(){
				if(this.flag){
					return this.ratings.filter((item)=>{
						return item .text
					})
				}
				return this.ratings;
			}
		},
		created(){
			this.$store.dispatch('getSeller');
			this.$store.dispatch('getRatings');
			
			this.$nextTick( () => {
				setTimeout(() => {
					this._ratingMenu();
				},100)
			});
			this.iconClassMap = ["decrease","discount","special","invoice","guarantee"]
		},
		components:{
			star
		},
		methods:{
			
			_ratingMenu(){
				this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
			},
			changeFlag(){
				
				this.flag = !this.flag;
				this.refresh();
				console.log(111)
			},
			refresh(){
				setTimeout(()=>{
					this.menuBS.refresh();
				},20)
			},
			checkyes(){
				this.sizeone=false;
				this.sizetwo=true;
				this.sizethree=false;
				this.yes=0;
				this.all=false;
				this.refresh();
				console.log(111)
			},
			checkno(){
				this.sizeone=false;
				this.sizetwo=false;
				this.sizethree=true;
				this.yes=1;
				this.all=false;
				this.refresh();
				console.log(111)
			},
			checkall(){
				this.sizeone=true;
				this.sizetwo=false;
				this.sizethree=false;
				this.yes=-1;
				this.all=true;
				this.refresh();
				console.log(111)
			}
		}
	}	
</script>

<style lang='less' scoped>
	.ratings{
		.rating_header{

			height:2.16rem;
			display: flex;
			border-bottom: 0.01rem solid rgba(7,17,27,0.1);
			.header_left{
				margin-top: 0.36rem;
				height:1.44rem;
				width:2.75rem;
				border-right:0.01rem solid rgba(7,17,27,0.1);
				text-align: center;
				p:nth-of-type(1){
					font-size: 0.48rem;
					color:rgb(255,153,0);
					line-height: 0.56rem;

				}
				p:nth-of-type(2){
					font-size: 0.24rem;
					color:rgb(7,17,27);
					line-height: 0.24rem;
					padding-top: 0.12rem;
				}
				p:nth-of-type(3){
					font-size: 0.24rem;
					color:rgb(147,153,159);
					line-height: 0.24rem;
					padding-top: 0.16rem;
					padding-bottom:0.12rem;
				}

			}
			.header_right{
				padding:0.36rem 0.48rem 0.36rem 0.48rem;
				li:nth-of-type(1){
					font-size: 0.24rem;
					color:rgb(7,17,27);
					line-height: 0.36rem;
					display: flex;
					span:nth-of-type(1){

					}
					.star1{
						padding-left: 0.24rem;
						padding-right: 0.24rem;
						.xx{
							width:0.36rem;
							height:0.36rem;
							margin-right: 0.16rem;
						}
					}
					span:nth-of-type(2){
						// font-size: 0.48rem;
						color:rgb(255,153,0);
						// line-height: 0.56rem;
					}
				}
				li:nth-of-type(2){
					font-size: 0.24rem;
					color:rgb(7,17,27);
					line-height: 0.36rem;
					display: flex;
					padding-top: 0.16rem;
					span:nth-of-type(1){

					}
					.star2{
						padding-left: 0.24rem;
						padding-right: 0.24rem;
						// .xx{
						// 	// width:0.36rem;
						// 	height:0.36rem;
						// 	margin-right: 0.16rem;
						// }
					}
					span:nth-of-type(2){
						// font-size: 0.48rem;
						color:rgb(255,153,0);
						// line-height: 0.56rem;
					}
				}
				li:nth-of-type(3){
					line-height: 0.36rem;
					font-size: 0.24rem;
					display: flex;
					padding-top: 0.16rem;
					span:nth-of-type(1){
						color:rgb(7,17,27);
						padding-right: 0.24rem;
					}
					span:nth-of-type(2){
						color:rgb(147,153,159);
					}

				}
			}
		}
		.divide{
			height:0.4rem;
			border-bottom: 0.01rem solid rgba(7,17,27,0.1);
			background-color: #f4f5f7;
			
		}
		.rating_count{
			border-bottom: 0.01rem solid rgba(7,17,27,0.1);
			.count_top{
				padding-top: 0.36rem;
				padding-bottom: 0.36rem;
				display: flex;
				// padding-left: 0.42rem;
				margin-left: 0.36rem;
				width:6.78rem;
				border-bottom: 0.01rem solid rgba(7,17,27,0.1);

				.count_all{
				// display: block;
					width:1.6rem;
					height:0.8rem;
					background-color: #00a0dc;
					text-align: center;
