					border-radius: 0.05rem;
					font-size: 0.3rem;
					color:white;
					margin-right: 0.16rem;
					&.comcolor{
						color:orange;
					}

				}
				.count_good{
				// display: block;
					width:1.6rem;
					height:0.8rem;
					background-color: lightblue;
					text-align: center;
					line-height: 0.8rem;
					border-radius: 0.05rem;
					font-size: 0.3rem;
					color:white;
					margin-right: 0.16rem;
					&.comcolor{
						color:orange;
