				}
				.count_bad{
				// display: block;
					width:1.6rem;
					height:0.8rem;
					background-color: lightgray;
					text-align: center;
					line-height: 0.8rem;
					border-radius: 0.05rem;
					font-size: 0.3rem;
					color:white;
					&.comcolor{
						color:orange;
					}

				}
			}
			
			.count_bottom{
				width:100%;
				height:1.08rem;
				color:rgb(147,153,159);
				line-height:1.08rem;
