		}
		.menu-wrapper{
			height:3.5rem;
			overflow: hidden;
		}
		.height{
			height:auto;
		}
		.ratings_container{
			display: flex;
			// height:30rem;
			overflow: hidden;
			.ratings_left{
				width:0.56rem;
				height:0.56rem;
				padding-top: 0.36rem;
				padding-left: 0.36rem;
				
				img{
					width:100%;
					height:100%;
					border-radius: 0.24rem;
					// padding-top: 0.36rem;
					// padding-left: 0.36rem;
				}
			}
			.ratings_right{
				padding-top: 0.36rem;
				padding-left: 0.24rem;

				.right_div1{
					font-size: 0.2rem;
					color:rgb(7,17,27);
					width:100%;
					.right_name{
						font-size: 0.2rem;
						color:rgb(7,17,27);
					}
					.right_time{
						padding-right: 0.36rem;
						padding-left: 3.4rem;
						text-align: right;
					}
				}
				.right_div2{
					padding-top: 0.08rem;
					font-size: 0.2rem;
					color: rgb(147,153,159);
					display: flex;
					span{
						padding-left: 3.4rem;
					}
				}
				.right_div3{
					padding-top: 0.12rem;
					padding-right: 0.36rem;
					font-size: 0.24rem;
					color:rgb(7,17,27);
					line-height: 0.36rem;
				}
				.right_div4{
					padding-top: 0.16rem;
					padding-right: 0.36rem;
					.icon-damuzhion{
						color:#00a0dc;
					}
					.icon-down{
						color:lightgray;
					}
					span{
						width:0.2rem;
						overflow: hidden;
						font-size: 0.18rem;
						border:0.01rem solid rgba(7,17,27,0.1);
						margin-right: 0.12rem;
						margin-left: 0.12rem;
						// white-space: nowrap;
						text-overflow:ellipsis;
						// display: inline-block;;
					}

				}
			}
		}
		// .space{
		// 	height:1.32rem;
		// 	width:100%;
		// }	
	}
</style>


