import types from '../types.js'
import axios from 'axios'
//相当于 .Vue里面的data
const state = {
	ratings:[],
	
}
//返回的数据
const getters = {
	ratings(state){
		return state.ratings;
	}
}
// 接收components里面传过来的方法并提交给mutations执行
const actions = {
	getRatings({commit,state}){
		axios.get('../../../static/data.json').then(resp=>{
			// if(resp.data.error === 0){
				commit(types.GET_RATINGS,resp.data.ratings);
			// }
		})
	}
}
// 执行具体动作
const mutations = {
	[types.GET_RATINGS](state,data){
		state.ratings = data;
	}
}
export default {
	state,
	getters,
	actions,
	mutations
}


