<template>
	<transition enter-active-class="animated slideInRight" leave-active-class="animated slideOutRight">
		<div class="goodsdetail" v-show="foodshow" ref="menuWrapper">
			<div class="detail_content">
				<div class="image_head">
					<img :src="foods.image" alt="">
				</div>
				<div class="back_icon" @click="hidefood">
					<i class="icon iconfont icon-shiliangzhinengduixiang4"></i>
				</div>
				<div class="goods_title">
					<div class="title_left">
						<p>{{foods.name}}</p>
						<div class="sell_count">
							<span >月售{{foods.sellCount}}份</span>
							<span>好评率{{foods.rating}}%</span>
						</div>
						<div class="foodprice">
							<span>￥<span class="foodprice_1">{{foods.price}}</span></span>
							
							<span v-if="foods.price">￥{{foods.price}}</span>
						</div>
					</div>
					<div class="title_right">
						<div class="shop_car" @click="add" v-show='!foods.count' :class="{'showlist':!foods.count}">
							<span>加入购物车</span>
						</div>
						<div class="iconadd" v-show='foods.count'>
							<iconadd class="creace" :food='foods'></iconadd>
						</div>
						
					</div>
				</div>
				<div class="divide"></div>
				<div class="food_dir" v-if="foods.info">
					<p>商品介绍</p>
					<div ><span>{{foods.info}}。</span></div>
				</div>
				<div class="divide" v-if="foods.info"></div>
				<div class="good_rating">
					<div class="goods_rating_top">
						<div class="rating_text">
							<p>商品评价</p>
						</div>
						<div class="count_top">
							<div class="count_all" @click="checkall"  :class="{'comcolor':sizeone}">
								<span v-if="foods.ratings" >全部{{foods.ratings.length}}</span>
							</div>
							<div class="count_good" @click="checkyes"  :class="{'comcolor':sizetwo}">
								<span>满意2</span>
							</div>
							<div class="count_bad" @click="checkno" :class="{'comcolor':sizethree}">
								<span>不满意1</span>
							</div>
						</div>
						<div class="count_bottom">
							<i class="iconfont icon-duihao" :class="{'checked':flag}" @click="changeFlag"></i>
							<span>只看有内容的评价</span>
						</div>
					</div>
					<div v-for="v in ratList" class="ratings_container" v-show="all || yes===v.rateType">
						<div class="rating_top">
							<div class="top_left">
							
								<span>{{changeTime(v.rateTime)}}</span>
							}
							}
							</div>
							<div class="top_right">
								<span>{{v.username}}</span>
								<img :src="v.avatar" alt="">
							</div>
						</div>
						<div class="rating_bottom">
							<i class="iconfont icon-damuzhion" v-if="v.rateType==0"></i>
							<i class="iconfont icon-down" v-if="v.rateType==1"></i>
							<span>{{v.text}}</span>
						</div>
						
					</div>
				</div>
				<div class="space"></div>
			</div>
			
		</div>
	</transition>
</template>

<script>
	import {mapGetters} from 'vuex'
	import BScroll from 'better-scroll'
	import iconadd from "../iconadd/iconadd.vue"
	import Vue from 'vue'
	export default {
		data(){
			return{
				all:true,
				yes:-1,
				flag:false,
				sizeone:true,
				sizetwo:false,
				sizethree:false
			}
		},
		components:{
	    	iconadd
        },
		computed:{
			...mapGetters([
				'foodshow',
				'foods'
			]),
			ratList(){
				if(this.flag){
					return this.foods.ratings.filter((item)=>{
						return item .text
					})
				}
				return this.foods.ratings;
			}

		},
		watch:{
            foods(){
                this.$nextTick( () => {
                    setTimeout( ()=>{
                        if( !this.menuBS ){
                            this.menuBS= new BScroll(this.$refs.menuWrapper, {
                                click: true
                            });
                        }else{
                            this.menuBS.refresh();
                        }
                    },1000)    
                });
            }
        },
			
		methods:{
			hidefood(){
				this.$store.dispatch('hidefood');
			},
			// _goodsMenu(){
			// 	this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
			// },
			add(){
				Vue.set(this.foods,'count',1);
			},
			changeTime(time){
				let time1 = new Date(time);
				let year=time1.getFullYear();
				let month=time1.getMonth();
				let date=time1.getDate();
				let hour=time1.getHours();
				let minute=time1.getMinutes();
				let second=time1.getSeconds();
				let timechange=year+"-"+month+"-"+date+" "+hour+":"+minute+":"+second;
				return timechange;
			},
			changeFlag(e){
				if( !e._constructed ){
					return;
				};
				this.flag = !this.flag;
				this.refresh();
				console.log(111)
			},
			refresh(){
				setTimeout(()=>{
					this.menuBS.refresh();
				},20)
			},
			checkyes(e){
				if( !e._constructed ){
					return;
				};
				this.sizeone=false;
				this.sizetwo=true;
				this.sizethree=false;
				this.yes=0;
				this.all=false;
				this.refresh();
				console.log(111)
			},
			checkno(e){
				if( !e._constructed ){
					return;
				};
				this.sizeone=false;
				this.sizetwo=false;
				this.sizethree=true;
				this.yes=1;
				this.all=false;
				this.refresh();
				console.log(111)
			},
			checkall(e){
				if( !e._constructed ){
					return;
				};
				this.sizeone=true;
				this.sizetwo=false;
				this.sizethree=false;
				this.yes=-1;
				this.all=true;
				this.refresh();
				console.log(111)
			}
		}
	}
</script>

<style lang="less" scoped>
	.goodsdetail{
		background-color: white;
		position:fixed;
		top: 0;
		z-index: 999;
		left: 0;
		right: 0;
		bottom: 0;
		
		.detail_content{
			// height:7.869rem;
			// height:20rem;
			.image_head{
				position: relative;
				width: 100%;
				height: 0;
				padding-bottom: 100%;
				img{
					position: absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top:0;
				}
			}
			.icon-shiliangzhinengduixiang4{
				position: fixed;
				font-size: 0.36rem;
				top:10px;
				left: 10px;
				width: 10px;
				height: 10px;
				// border: 1px solid red;
				// padding:10px;
				color: #bdf2f8;
			}
			.goods_title{
				display: flex;
				border-bottom: 0.01rem solid rgba(7,17,27,0.1);
				.title_left{
					p{
						padding-top: 0.36rem;
						padding-left: 0.36rem;
						font-size: 0.32rem;
						font-weight: 600;
						line-height: 0.28rem;
						color: rgb(7,17,27);
					}
					.sell_count{
						margin-top: 0.16rem;
						padding-left: 0.36rem;
						font-size: 0.2rem;
						color: rgb(147,153,159);
						line-height: 0.2rem;
						span{
							padding-right: 0.24rem;
						}
					}
					.foodprice{
						margin-top: 0.36rem;
						margin-bottom: 0.36rem;
						padding-left: 0.36rem;
						span:nth-of-type(1){
							font-size: 0.2rem;
							color: rgb(240,20,20);
							vertical-align: bottom;
							.foodprice_1{
								font-size: 0.32rem;
							}
						}
						span:nth-of-type(2){
							color: rgb(147,153,159);
							font-weight: 700;
							// line-height: 0.48rem;
						}
					}
				}
				.title_right{
					// position:relative;
					.iconadd{
						position: absolute;
						right:0.36rem;
						margin-top: 1.36rem;
					}
					.showlist{
						// filter:blur(0.1rem);
						// background:rgba(7,17, 27, 0.5);
					}
					.shop_car{
						font-size: 0.2rem;
						color:rgb(255, 255, 255);
						line-height: 0.48rem;
						text-align: center;
						background-color: rgb(0,160,220);
						border-radius: 0.24rem;
						width:1.48rem;
						position: absolute;
						right:0.36rem;
						margin-top: 1.36rem;
					}
				}

			}
			.divide{
			height:0.4rem;
			border-bottom: 0.01rem solid rgba(7,17,27,0.1);
			background-color: #f4f5f7;
			}
			.food_dir{
				border-bottom: 0.01rem solid rgba(7,17,27,0.1);
				p{
					font-size: 0.32rem;
					padding-top: 0.36rem;
					padding-left: 0.36rem;
					font-weight:400;
				}
				div{
					width:6.68rem;
					margin-left: 0.36rem;
					margin-top: 0.12rem;
					margin-bottom: 0.36rem;
					span{
						padding-left: 0.16rem;
						padding-right: 0.16rem;
						font-size: 0.24rem;
						font-weight: 200;
						color:rgb(77,85,93);
						line-height: 0.48rem;

					}
				}
			}
			.good_rating{
				.goods_rating_top{
					.rating_text{
						p{
							font-size: 0.32rem;
							padding-top: 0.36rem;
							padding-left: 0.36rem;
							font-weight:400;
							padding-top: 0.36rem;
						}
					}
					.count_top{
							padding-top: 0.36rem;
							padding-bottom: 0.36rem;
							display: flex;
							// padding-left: 0.42rem;
							margin-left: 0.36rem;
							width:6.78rem;
							border-bottom: 0.01rem solid rgba(7,17,27,0.1);
							.count_all{
							// display: block;
								width:1.6rem;
								height:0.8rem;
								background-color: #00a0dc;
								text-align: center;
								line-height: 0.8rem;
								border-radius: 0.05rem;
								font-size: 0.3rem;
								color:white;
								margin-right: 0.16rem;
								&.comcolor{
									color:orange;
								}
							}
							.count_good{
							// display: block;
								width:1.6rem;
								height:0.8rem;
								background-color: lightblue;
								text-align: center;
								line-height: 0.8rem;
								border-radius: 0.05rem;
								font-size: 0.3rem;
								color:white;
								margin-right: 0.16rem;
								&.comcolor{
									color:orange;
								}
							}
							.count_bad{
							// display: block;
								width:1.6rem;
								height:0.8rem;
								background-color: lightgray;
								text-align: center;
								line-height: 0.8rem;
								border-radius: 0.05rem;
								font-size: 0.3rem;
								color:white;
								&.comcolor{
									color:orange;
								}
							}
					}
					.count_bottom{
						width:100%;
						height:1.08rem;
						color:rgb(147,153,159);
						line-height:1.08rem;
						font-size: 0.28rem;
						padding-left: 0.36rem;
						border-bottom: 0.01rem solid rgba(7,17,27,0.1);
						.icon-duihao{
							font-size: 0.48rem;
						}
						.checked {
			                color: rgb(0, 160, 220);
			            }
					}
					
				}
				.ratings_container{

					.rating_top{
						display: flex;
						// font-size: 0.2rem;
						// color:rgb(7,17,27,);
						// line-height: 0.24rem;
						margin-top: 0.32rem;
						.top_left{
							font-size: 0.2rem;
							color:rgb(147,153,159);
							line-height: 0.24rem;
							padding-left: 0.36rem;
						}
						.top_right{
							position: absolute;
							right:0.36rem;
							font-size: 0.2rem;
							color:rgb(147,153,159);
							line-height: 0.24rem;
							img{
								border-radius: 0.16rem;
								width:0.28rem;
								height:0.28rem;
								// padding-left: 0.12rem;
								line-height: 0.24rem;
							}
						}

					}
					.rating_bottom{
						width:6.68rem;
						margin-left: 0.36rem;
						border-bottom: 0.01rem solid rgba(7,17,27,0.1);
						.icon-damuzhion{
							color:#00a0dc;
						}
						.icon-down{
							color:lightgray;
						}
						// padding-left: 0.36rem;
						font-size: 0.24rem;
						color:rgb(7,17,27);
						margin-top: 0.12rem;
						padding-bottom: 0.32rem;
					}
				}
				
					
			}
		}
		.space{
			height:1.32rem;
			width:100%;
		}
	}
</style>


