<template>
<div class="bgblack">	
	<div class="shopcar" >
		<div class="carleft">
			<div class="car">
				<div  >
					<div class="caricon" @click='_showlist ' :class='{"caricon1":totalCount}'>
						<i class="iconfont icon-gouwuche" :class='{"caricon2":totalCount}'></i>
						
					</div>
				</div>				
				<div class="num" v-if="!totalCount==0">
					<span>{{totalCount}}</span>
				</div>				
			</div>
			<div class="cost">
				<span>￥{{totalPrice}}</span>
			</div>
			<div class='ship'>
				<span>另需配送费￥4元</span>
			</div>
		</div>
		<div class="carright" :class='{"enough":totalPrice >= seller.minPrice}'>
			<p>{{payDesc}}</p>
		</div>
		<!-- <carfoods :carfood="selectFoods"  v-show='a'></carfoods> -->
		
		<transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" >
			<div class="carfoods" v-show="carshow">
					<div class="carbody carbody-hook">
						<div class="car" v-if='totalCount'>
							<div class="cartxt">
								购物车
							</div>
							<div class="clear"  @click='clearfood'>
								清空
							</div>
						</div>
						
						<div class="foodlist" ref="foodScroll">
							<ul >
								<li v-for='v in selectFoods' class="foodli">
									<div class="eachline">
										<span class="name">{{v.name}}</span>
										<div class="price">
											<span class="pri">￥{{v.price}}</span>
											
											<Cartcontrol :food='v' class="addOrdele"></Cartcontrol>	
										</div>
									</div>	
								</li>
							</ul>	
						</div>

					</div>

			
		</div>
		</transition>
	</div>
</div>
</template>

<script>
	import  {mapGetters} from 'vuex'
	import Vue from 'Vue'
	import Cartcontrol from '../iconadd/iconadd.vue'
	import BScroll from 'better-scroll'
	export default {
		data(){
			return {
				a:false
				
			}
		},
		created(){
			
			
		},
		computed:{
			...mapGetters([
				'seller',
				'goods'
			]),
			carshow(){
				if(!this.totalCount){
					this.a=false;
					return false;
				}
				return this.a

			},
			selectFoods(){
				let foods = [];
				this.goods.forEach(good => {
					good.foods.forEach(food=>{
						if(food.count>0){
							foods.push(food);
						}
					})
				})
				return foods;
			},
			totalPrice(){
				let total= 0;
				this.selectFoods.forEach(food=>{
					total+=food.price * food.count;
				});
				return total;
			},
			totalCount(){
				let count = 0;
				this.selectFoods.forEach(food=>{
					count += food.count;
				});
				return count;
			},
			payDesc(){
				if(this.totalPrice === 0){
					return `￥${this.seller.minPrice}元起送`
				}else if(this.totalPrice<this.seller.minPrice){
					return `还差${this.seller.minPrice - this.totalPrice}元起送`
				}else{
					return "去结算"
				}
			}
		},
		components:{
			Cartcontrol
		},
		methods : {
			
			clearfood(){
				this.selectFoods.forEach((food)=>{
				  	food.count=0;
				})
				this.a=!this.a;
			},
			// _initFood(){
			// 	this.scrollFood = new BScroll(this.$refs.foodScroll,{
			// 		click:true
			// 	}); 
			// },
			_showlist(){
				this.a=this.totalCount?!this.a:false;
				this.$nextTick(()=>{ 
					setTimeout(()=>{
						if( !this.menuBS ){
                            this.menuBS= new BScroll(this.$refs.foodScroll, {
                                click: true
                            });
                        }else{
                            this.menuBS.refresh();
                        }
					
					},100)
				});

			}
		}
	}

</script>

<style lang='less'>
.bgblack{
	height: 100%;
	width: 100%;
	background-color: rgba(255,255,255,0.5);
	.shopcar{
		position: fixed;
		bottom: 0;
		width: 100%;
		height: 1rem;
		background-color: #141d27;
		display: flex;
		z-index: 1000;
		.carleft{
			display: flex;
			width: 6rem;
			height: 1rem;
			background-color: #141d27;
			.num{
				width: 0.48rem;
				height: 0.32rem;
				line-height: 0.34rem;
				border-radius: 0.16rem;
				background-color: red;
				position: fixed;
				left:0.9rem;
				bottom: 0.9rem;
				color:#fff;
				text-align: center;
				font-size: 0.2rem;
			}
			.cost{
				// font-weight: 800;
				font-size: 0.38rem;
				line-height: 1rem;
				color:#919396;
				padding-right: 0.2rem;
				border-right:1px solid #2b333b;

			}
			.ship{
				font-size: 0.32rem;
				color: #919396;
				line-height: 1rem;
				padding-left: 0.3rem;
			}
			.car{
				// display: flex;
				width: 1rem;
				padding: 0.3rem;
				
				div:nth-of-type(1){
					width: 1rem;
					height: 1rem;
					border-radius: 1rem;
					background-color: #141d27;
					// margin-top: 1rem;
					position: fixed;
					bottom: 0.2rem;
					text-align: center;
					line-height: 1rem;
					
					.caricon{
						&.caricon1{
						background-color:rgb(0,180,220);
					}
						width: 0.8rem;
						height: 0.8rem;
						position: fixed;
						bottom: 0.3rem;
						border-radius: 0.8rem;
						background-color: #2b343c;
						margin-left: 0.1rem;
						line-height: 0.7rem;
						.icon-gouwuche{
							font-size: 0.48rem;
							color:#80858a;
							&.caricon2{
								color:rgb(255,255,255);
							}
						}
						
						

					}
				}
			}
		}
		.carright{
			width: 2.1rem;
			height: 1rem;
			background-color: #2b333b;
			text-align: center;
			line-height: 1rem;
			color:#979a9c;
			p{
				font-size: 0.24rem;
				font-weight: 700;
				
				
			}
			&.not-enough{
                background: red;
                color:#979a9c;
            }
			&.enough{
				color:#fff;
				background-color:#2ba431;
			}

		}

		.carfoods{
		width:100%;
		position: fixed;
		z-index: -1;
		top:0;
		left:0;
		right:0;
		bottom:0;
		background-color:rgba(7,17,27,0.8);
		.carbody{
			width:100%;
			position:fixed;
			z-index:90;
			bottom:1rem;
			background-color:white;
			.car{
				width:100%;
				height:0.8rem;
				background-color:#f3f5f7;
				border-bottom:rgba(7,17,27,0.1) 1px solid;
				line-height:0.8rem;
				box-sizing:border-box;
				padding:0 0.36rem;
				display:flex;
				justify-content:space-between;
				.cartxt{
					font-size:0.28rem;
					font-weight:200;
					color:rgb(7,17,27);
				}
				.clear{
					font-size:0.24rem;
					color:rgb(0,160,220);
				}
			}
		}
		.foodlist{
			width:100%;
			max-height:4.5rem;
			overflow:hidden;
			ul{
				width:100%;
				overflow:hidden;
				max-height:100rem;
			}
			.foodli{
				width:100%;
				padding:0 0.36rem;
				box-sizing:border-box;
				.eachline{
					height:0.96rem;
					box-sizing:border-box;
					padding:0.24rem 0;
					display:flex;
					justify-content:space-between;
					border-bottom:rgba(7,17,27,0.1) 1px solid;
					.name{
						display:inline-block;
						font-size:0.28rem;
						color:rgb(7,17,27);
						line-height:0.48rem;
					}
					.price{
						display:inline-block;
						.pri{
							font-size:0.28rem;
							font-weight:500;
							color:rgb(240,20,20);
							line-height:0.48rem;
							display:inline-block;
							padding-right:0.24rem;
						}
						.addOrdele{
							display:inline-block;
						}
					}
				}
			}
		}
	}
}
}

</style>


