<template>
	<div class="icon_add">
		<transition name = 'move'>
			<i class="iconfont icon-jianhao" v-show='food.count' @click.stop="decrease"></i>
		</transition>
		<transition name = 'move'>								
			<span v-show='food.count'>{{food.count}}</span>	
		</transition>				
		<i class="iconfont icon-jiahao" @click.stop='increase'></i>								
	</div>
</template>
<script>
	import Vue from 'vue';
	import {mapGetters} from 'vuex'
	export default {
		props:['food'],
		methods:{
			increase(){
				if( !this.food.count ){
					Vue.set(this.food,'count',1);
				}else{

					this.food.count++;
				}
			},
			decrease(){
				if(this.food.count){
					this.food.count -- ;
				}
			}
		}
	}
</script>
<style lang="less">
	.icon_add{
		 .move-enter-active, .move-leave-active {
            transition: all 0.2s;
        }
        .move-leave-active {
            transition: all 0.2s;
            transform:translate(30px) rotate(90deg);
        }
		.icon-jianhao{
			color:rgb(0,160,220);
			font-size: 0.48rem;
			line-height: 0.48rem;
		}
		span{
			font-size: 0.32rem;
			color: rgb(147,153,159);
			line-height: 0.48rem;
			font-weight: 600;
			padding-left: 0.12rem;
			padding-right: 0.12rem;

		}
		.icon-jiahao{
			color:rgb(0,160,220);
			font-size: 0.48rem;
			line-height: 0.48rem;
		}
	}
</style>


